import styled from "styled-components"

const Anchor = styled.a`
  color: darkturquoise;
  font-size: 24px;
  text-decoration: none;
  font-weight: 100;
  &:hover,
  &:focus {
    text-decoration: underline;
    cursor: "progress"};
  }
`

export default Anchor



